import React from "react"
import { Link, graphql } from "gatsby"
import Layout from "../components/layout"
import CourseLayout from "../components/courseLayout"

import ReactMarkdown from "react-markdown"
import { Table } from "react-bootstrap" 



const CourseRating = ({ pageContext: { course } }) => {
	const courseInfo = `

	`;

		return (

		  <Layout>
		  	<CourseLayout>
		    	<h2>Rating</h2>
		    	<p  className="pHomework">{course.breed}</p>

		    	<ReactMarkdown source={courseInfo} />

		    	<Table striped bordered hover size="sm">
				  <thead>
				    <tr>
				      <th>#</th>
				      <th>Student</th>
				      <th>Rating</th>
				      <th>Final Grade</th>
				    </tr>
				  </thead>
				  <tbody>
				  {course.rating.map(rating =>
				    <tr>
				      <td>{rating.id}</td>
				      <td>{rating.firstname}</td>
				      <td>{rating.rating}</td>
				      <td>

				      {(() => {
				        if (rating.rating >= 85) {
					        return '5';
				        }
				        else if(rating.rating >= 60 && rating.rating < 85) {
				        	return '4';
				        }
				        else if(rating.rating >= 40 && rating.rating < 60) {
				        	return '3';
				        }
				        else {
				        	return '2';
				        }
				      })()}

				      </td>
				    </tr>
				    )}
				  </tbody>
				</Table>

		    </CourseLayout>
		  </Layout>

	)
}

export default CourseRating
